.woo-products-block {
    padding: 20px;
}

.products-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
}

.product-card {
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 15px;
    transition: transform 0.2s;
}

.product-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 5px 15px rgba(0,0,0,0.1);
}

.product-card a {
    text-decoration: none;
    color: inherit;
}

.product-card img {
    width: 100%;
    height: auto;
    border-radius: 4px;
}

.product-card h3 {
    margin: 10px 0;
    font-size: 1.1em;
    color: #333;
}

.product-card .price {
    color: #0066cc;
    font-weight: bold;
}

/* Editor Styles */
.product-card.placeholder {
    background: #f5f5f5;
    min-height: 300px;
}

.product-card.placeholder .product-image {
    background: #e0e0e0;
    width: 100%;
    height: 200px;
    border-radius: 4px;
}

@media (max-width: 768px) {
    .products-grid {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 480px) {
    .products-grid {
        grid-template-columns: 1fr;
    }
}
